@import "../common/styles/variebleSCSS";
@import "../common/button/Button.module";;
.project {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 10px 20px 10px 20px;

    @media screen and (max-width: 650px) {
        width: auto;
    }
    .img {
        width: 100%;
        height: 300px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        background-size: cover;
        border: 2px solid $backgroundColor;
        position: relative;
        background-position: top;
        border-radius: 8px;
        transition: 0.14s  0s;
        &:hover {
            border: 2px solid $secondColor;
          transform: scale(1.1)
        }
        &::before{
            position: absolute;
            content: '';
            background-color: $backgroundColor;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            opacity: 0.4;
        }
        .watch {
            @extend .btn;
            align-self: center;
            border-radius: 50px;
            background-color: grey;
            width: 100px;
            font-size: 18px;
        }
        @media screen and (max-width: 650px) {
            height: 240px;
            padding: 5px;
        }
    }
    .title {
        display: flex;
        font-weight: 400;
        line-height: 1.2em;
        font-size: 20px;
        margin-bottom: 16px;
        color: #fdfeff;
    }
    .description {
        color: #a9adb8;
        background-color: $backgroundColor;
        font-family: 'Inter', sans-serif;
        font-size: 16px;
        font-weight: 400;
        line-height: 1.6em;
    }
}



